
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
 
  .modal-content {
    background: rgb(255, 255, 255);
    border-radius: 8px;
    width: 400px;
    text-align: center;
    line-height: 200%;
  }
  
  .modal-buttons {
    margin-top: 10px;
    gap: 20px;
  }
  
  .modal-buttons button {
    margin: 0 10px;
    cursor: url('../Assets/beer.png'), auto;;
    padding-top: 5px;
    padding-bottom: 5px;

  }
  
  .price {
    color: black;
    padding-left: 20px;
    display: flex;
    gap: 10px;
    margin-left: 12px;
    padding-bottom: 4%;
    text-align: left;
  }
  
  .amount {
    /* color: black;
    display: flex;
    padding-left: 50px;
    text-align: left; */
    color: black;
    padding-left: 20px;
    display: flex;
    gap: 10px;
    margin-left: 1px;
    padding-bottom: 4%;
    text-align: left;
    columns: auto;
  }

  .price-box {
    margin-left: 9px;
  }

